import React, { memo, useRef, useState } from "react";
import {
  AnimatedKeyboardAvoidingViewRef,
  DynamicHeader,
  MyKeyboardAvoidingView,
  MyTextInput,
  ScreenWrapper
} from "@true/ui";
import { useAutoToastError, useNavigationParams } from "@true/core";
import { styled, translate, useTheme } from "@true/core";
import { BottomButton } from "@policy/components/Button/BottomButton";
import { useAsyncFn } from "react-use";
import { requestApprove } from "@policy/store/policies/functions";
import { StyleSheet } from "react-native";
import Animated from "react-native-reanimated";
import { Colors } from "@true/core";
import { goBack } from "@true/core";

export interface ApproveScreenProps {
  id: string | number;
  title: string;
  status: 1 | -1;
}

export const ApproveScreen = memo(() => {
  const {title, status, id} = useNavigationParams<ApproveScreenProps>();

  const [note, setNote] = useState('');

  const [{loading: approveLoading, error}, onApprove] = useAsyncFn(async () => {
    await requestApprove({
      id,
      status,
      note,
    });
    goBack();
  }, [id, status, note]);

  useAutoToastError(error);

  const keyboardRef = useRef<AnimatedKeyboardAvoidingViewRef | null>(null);

  const theme = useTheme();

  return (
    <ScreenWrapper>
      <MyKeyboardAvoidingView ref={keyboardRef}>
        <DynamicHeader title={title} />
        <Animated.ScrollView ref={keyboardRef.current?.scrollRef}>
          <SInputWrapper>
            <MyTextInput
              value={note}
              title={
                translate('policy.note') +
                ' (' +
                translate('policy.not_required') +
                ')'
              }
              keyName={''}
              onTextChange={(_, value) => {
                setNote(value);
              }}
            />
          </SInputWrapper>
        </Animated.ScrollView>
        <BottomButton
          buttons={[
            {
              title:
                status == 1
                  ? translate('policy.approve')
                  : translate('policy.reject'),
              onPress: onApprove,
              textColor: Colors.white,
              backgroundColor: status == 1 ? Colors.green1 : Colors.red1,
              loading: approveLoading,
            },
          ]}
        />
      </MyKeyboardAvoidingView>
    </ScreenWrapper>
  );
});

const SInputWrapper = styled.View`
  margin: 16px 16px 16px 16px;
`;

const styles = StyleSheet.create({
  input: {},
});
